@import "../varibles.scss";
@import "../breakpoints.scss";

.intro-overlay {
  .top {
    height: 70vh;
    height: calc(var(--vh, 1vh) * 70);
    position: absolute;
    width: 100%;
    z-index: 8;

    .overlay-top {
      position: absolute;
      height: 100%;
      width: 33.333vw;
      background: $black;
      bottom: 0;
      left: 0;
      right: 0;

      @include media("<=tablet") {
        width: 100vw;
      }

      &:nth-child(2) {
        left: 33.333%;

        @include media("<=tablet") {
          display: none;
        }
      }

      &:nth-child(3) {
        left: 66.666%;

        @include media("<=tablet") {
          display: none;
        }
      }
    }
  }

  .bottom {
    height: 30vh;
    height: calc(var(--vh, 1vh) * 30);
    position: absolute;
    bottom: 0;
    width: 100%;
    z-index: 8;

    .overlay-bottom {
      position: absolute;
      height: 100%;
      width: 33.333vw;
      background: $black;
      bottom: 0;
      right: 66.666%;

      @include media("<=tablet") {
        right: 0;
        width: 100vw;
      }

      &:nth-child(2) {
        right: 33.333%;

        @include media("<=tablet") {
          width: 100vw;
          top: 100%;
          right: 0;
        }
      }

      &:nth-child(3) {
        right: 0;

        @include media("<=tablet") {
          width: 100vw;
          top: 200%;
        }
      }
    }
  }
}
