@import "../varibles.scss";
@import "../breakpoints.scss";

.main {
  height: 70vh; 
  height: calc(var(--vh, 1vh) * 70);
  background: $gray-gradient;

  .row-banner {
    padding: 0px 32px;
    display: flex;
    height: 100%;
    justify-content: center;
    position: relative;
    overflow: hidden;
 
    .photo{
      position: absolute;
      display: flex;
      bottom: -20%;
      height: 70vh; 
      height: calc(var(--vh, 1vh) * 70);
      
      img{
         filter: grayscale(70%);
      }
    }

    h2 {
      position: absolute;
      width: 100%;
      top:35%;
      font-size: 3rem;
      line-height: 4.2rem;
      font-weight: 700;
      z-index: 100;
      color: $white;
      mix-blend-mode: difference;
      
      @include media("<=tablet") {
        font-size: 2.4rem;
        line-height: 3rem;
      }

      @include media("<=phone") {
        margin-top: 72px;
        font-size: 1.7rem;
        line-height: 2.2rem;
        
      }
      
      .line {
        margin-bottom: 8px;
        position: relative;
        margin: 0 32px;

        @include media("<=phone") {
          margin: 0 16px;
        }
      }
    }
  }
}
